@import "base/variables";
@import "base/mixins";
@import "base/typography";
@import "vendor/normalize";
@import "base/manifest";
@import "components/sprite";
body {
    font-family: $font-text, Arial, sans-serif;
    width: 100%;
    height: 100%;
    line-height: 24px;
    font-size: 16px;
    font-weight: 400;
    color: #4A4A4A;
}
a{
    text-decoration: none;
    @include trans(0.3);
}
a:hover{
    color: $accent-color;
}
section p{
    padding: 13px 0;
}
nav.menu{
	ul{
		li{
			display: inline-block;
			margin: 0 20px;	
			position: relative;
			a{
				position: relative;
			}
			&:last-child{
				margin-right: 0;
			}
			&:first-child{
				margin-left: 0;
			}
			&.active:before, &:hover:before{
				position: absolute;
				content: "";
				width: 20px;
				height: 15px;
				background:$btn-color;
				left: 0;
				right: 0;
				bottom: -13px;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
}
.slogan{
	max-width: 710px;
	background: $bg-color;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	padding: 22px 15px;
	font-size: 25px;
	line-height: 31px;
}
.item-service{
	margin: 0 15px;
	max-width: 300px;
	width: 100%;
	box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.1);
	height: 550px;
	border-radius: 5px;
	.img{
		img{
			border-top-right-radius: 5px;
			border-top-left-radius: 5px;
		}
	}
	.context{
		padding: 30px;
	}
	.ttl{}
	.txt{
		margin-top: 10px;
		text-align: center;
		color: $link-color;
	}
	.icons{
		margin-top: 30px;
		display: flex;
		flex-wrap: wrap;
		.itm{
			display: flex;
			align-items: center;
			width: 50%;
			margin: 10px 0;
			img{
				width: 24px;
				height: 24px;
			}
			span{
				margin-left: 10px;
			}
		}
	}
	.btn{
		margin-top: 40px;
	}
	&:first-child{
		margin-left: 0;
	}
	&:last-child{
		margin-right: 0;
	}
}
header{
	background-color: $bg-color;
    min-height: 764px;
    width: 100%;
    background: url(../img/bg.webp) center no-repeat;
	.pannel-bg{
		height: 93px;
		background: rgba(210, 101, 249, 0.05);
	}
	.logo{
		a{
			img{
				width: 170px;
				height: 60px;
			}
		}
	}
	.nav-menu{
		display: flex;
	    flex-direction: column;
	    align-items: flex-end;
	    margin-top: 12px;
		.ph-cb{
			max-width: 480px;
			width: 100%;
		}
		.phone{
			img{
				width: 17px;
				height: 17px;
			}
			a{
				margin-left: 15px;
				color: #000;
				font-size: 16px;
				span{
					font-size: 20px;
					font-weight: 500;
				}
			}
		}
		.callback{}
	}
	.utp{
		.ttl{
			margin-top: 65px;
		}
		.slogan{
			margin-top: 50px;
		}
		.stock{
			margin-top: 50px;
			font-size: 20px;
			max-width: 366px;
			margin-left: auto;
			margin-right: auto;
			span{
				margin-left: 20px;
			}
			img{
				width: 49px;
				height: 49px;
			}
		}
		.btn{
			margin-top: 55px;
			button{
				max-width: 300px;
				height: 50px;
				color: #000;
				font-weight: 500;
				background: $btn-color;
				text-transform: uppercase;
			}
		}
	}
}
.gall{
	text-align: center;
	.flex-gallery{
		margin-top: 30px;
		flex-wrap: wrap;
		margin-left: -15px;
		margin-right: -15px;
		.item{
			max-width: 185px;
			width: 100%;
			max-height: 130px;
			height: 130px;
			overflow: hidden;
			margin: 10px;
			border-radius: 4px;
			img{
				border-radius: 4px;
			}
		}
	}
}
.calculator{
	.col-4{
		max-width: 400px;
		width: 100%;
		min-height: 326px;
		background: #FFFFFF;
		box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.1);
		border-radius: 6px;
		padding: 10px 30px;
		.numb{
			.pisloches{
				font-weight: 900;
				font-size: 58px;
				line-height: 73px;
				color: #C4C4C4;
			}
			.txt{
				margin-left: 10px;
			}
		}
		.item{
			width: 33%;
			display: flex;
			align-items: center;
			margin-bottom: 48px;
			img{
				width: 24px;
				height: 24px;
			}
			.nadpis{
				margin-left: 10px;
			}
		}
		.flex-calc{
			flex-wrap: wrap;
		}
	}
	.my_check{
		max-width: 100%;
		flex-wrap: wrap;
		.oplet{
			width: 50%;
			margin-bottom: 17px;
		}
		position: relative;
		input[type="checkbox"] {
		    display: none;
		}
		input[type="checkbox"] + label {
		    cursor: pointer;
		    padding-left: 0;
		}
		input[type="checkbox"] + label::before {
		    content: "";
		    display: inline-block;
		    height: 22px;
		    width: 24px;
		    margin: 0 5px 0 0;
		    background: url("../img/checkboxes.png");
		}
		input[type="checkbox"]:checked + label::before {
		    background-position: 0 -22px;
		}
	}
	.window{
		margin-top: 15px;
		max-width: 100%;
		width: 100%;
		max-height: 60px;
		overflow: hidden;
		img{
			max-width: 100%;
			width: 100%;
			height: auto;
		}
	}
}

.nopoka{
	display: none;
	border-top: 1px solid #E5E5E5;
}
.gall{
input {
  display: none;
}

label {
  display: inline-block;
  margin: 0 0 -1px;
  padding: 5px 25px;
  font-weight: 400;
  text-align: center;
  color: #000;
  border: 1px solid transparent;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

label:before {
  font-family: fontawesome;
  font-weight: normal;
  margin-right: 10px;
}
label:hover {
  color: $accent-color;
  cursor: pointer;
}

input:checked + label {
  color: $link-color;
  border: 1px solid #E5E5E5;
  border-top: 1px solid $btn-color;
  border-bottom: 1px solid #fff;
}

#tab1:checked ~ #content1,
#tab2:checked ~ #content2,
#tab3:checked ~ #content3,
#tab4:checked ~ #content4 {
  display: block;
}

@media screen and (max-width: 800px) {
  label {
    font-size: 0;
  }
  label:before {
    margin: 0;
    font-size: 18px;
  }
}

@media screen and (max-width: 500px) {
  label {
    padding: 15px;
  }
}
}
